<template>
  <el-table
    :data="tableData"
    border
    style="width: 100%">
    <!-- 盘口1 -->
    <el-table-column align="center" label="盘1">
      <el-table-column
        prop="home_data_list[0].OD"
        :label="home_name"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="home_data_list[0].HD"
        label="盘口"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="away_data_list[0].OD"
        :label="away_name"
        align="center"
        >
      </el-table-column>
    </el-table-column>
    <!-- 盘口2 -->
    <el-table-column align="center" label="盘2">
      <el-table-column
        prop="home_data_list[1].OD"
        :label="home_name"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="home_data_list[1].HD"
        label="盘口"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="away_data_list[1].OD"
        :label="away_name"
        align="center"
        >
      </el-table-column>
    </el-table-column>
    <!-- 盘口3 -->
    <el-table-column align="center" label="盘3">
      <el-table-column
        prop="home_data_list[2].OD"
        :label="home_name"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="home_data_list[2].HD"
        label="盘口"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="away_data_list[2].OD"
        :label="away_name"
        align="center"
        >
      </el-table-column>
    </el-table-column>
    <!-- 盘口4 -->
    <el-table-column align="center" label="盘口4">
      <el-table-column
        prop="home_data_list[3].OD"
        :label="home_name"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="home_data_list[3].HD"
        label="盘口"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="away_data_list[3].OD"
        :label="away_name"
        align="center"
        >
      </el-table-column>
    </el-table-column>
    <!-- 盘口5 -->
    <el-table-column align="center" label="盘5">
      <el-table-column
        prop="home_data_list[4].OD"
        :label="home_name"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="home_data_list[4].HD"
        label="盘口"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="away_data_list[4].OD"
        :label="away_name"
        align="center"
        >
      </el-table-column>
    </el-table-column>
    <!-- 时间 -->
    <el-table-column
      prop="add_time"
      label="变化时间"
      align="center"
      width="250">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'asia',
  data () {
    return {
      home_name: this.$route.query.home_name,
      away_name: this.$route.query.away_name,
      tableData: []
    }
  },
  mounted () {
    // 循环加载数据
    this.initData()
    clearInterval(this.intervalId)
    this.intervalId = setInterval(() => {
      this.initData()
    }, 2000)
  },
  methods: {
    initData () {
      this.$axios.get('/event/list.json?comm_id=' + this.$route.query.comm_id)
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }
          this.tableData = respData.result
        })
    }
  },
  destroyed () {
    clearInterval(this.intervalId)
  }
}
</script>

<style scoped>
</style>



