<template>
  <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column label="亚盘" align="center">
      <el-table-column
        prop="home_odds"
        :label="home_name"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="home_handicap"
        label="盘口"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="away_odds"
        :label="away_name"
        align="center"
        >
      </el-table-column>
      <el-table-column
        prop="add_time"
        label="变化时间"
        align="center"
        width="250">
      </el-table-column>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'asia',
  data () {
    return {
      intervalId: '',
      home_name: this.$route.query.home_name,
      away_name: this.$route.query.away_name,
      tableData: []
    }
  },
  mounted () {
    this.initData()
    clearInterval(this.intervalId)
    this.intervalId = setInterval(() => {
      this.initData()
    }, 2000)
  },
  methods: {
    initData () {
      console.dir(this.$route.query)

      this.$axios.get('/asia/list.json?comm_id=' + this.$route.query.comm_id)
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }
          this.tableData = respData.result
        })
    }
  },
  destroyed () {
    clearInterval(this.intervalId)
  }
}
</script>

<style scoped>
</style>



