<template>
  <div id="app">
    <el-container>
      <el-header>
        <el-row>
          <el-button type="primary" @click="goTo('/')">主页</el-button>
          <el-button type="primary" @click="goTo('/league')">联赛列表</el-button>
        </el-row>
      </el-header>
      <el-main>
        <router-view/>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods: {
    goTo (target) {
      this.$router.push(target)
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



