<template>
  <el-table
    :data="tableData"
    border
    style="width: 98%; margin:0 auto;">
    <el-table-column
      prop="league_id"
      label="赛事ID"
      align="center"
      width="200">
    </el-table-column>
    <el-table-column
      prop="league_name"
      label="赛事"
      align="center"
      width="">
    </el-table-column>
    <el-table-column
      prop="league_name_cn"
      label="赛事中文"
      align="center"
      width="">
    </el-table-column>
    <el-table-column
      prop="add_time"
      label="入库时间"
      align="center"
      width="200">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'league',
  data () {
    return {
      tableData: []
    }
  },
  mounted () {
    this.initData()
  },
  methods: {
    initData () {
      this.$axios.get('/league/list.json')
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }
          this.tableData = respData.result
        })
    }
  }
}
</script>
<style scoped>
</style>



